.App{

  height: 100vh;
  margin: 0;
  padding-top: 25px;
  background-color: #1b1b32;
  font-family: Lato, sans-serif;
  display: flex;
flex-wrap: wrap;
justify-content: center;
padding: 20px;
}
.title{
  color: white;
  width: 100%;
  height: 50px;
  margin: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.calculator-container{
  width: 400px;
  height: 600px;
  padding: 14px;
  background-color: #99c9ff;
  border-radius: 20px;
  border: 5px solid white;
}
.row{
  margin: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}